import { Injectable } from "@angular/core";
import { BehaviorSubject } from "rxjs/BehaviorSubject";

import { Observable } from "rxjs/observable";
import { of } from "rxjs/observable/of";

import { Log } from "../models/log";

@Injectable()
export class LogService {
  logs: Log[];
  // SOURCE
  private logSource = new BehaviorSubject<Log>({
    id: null,
    text: null,
    date: null
  });
  //ACTUAL OBSERVABLE Properties
  selectedLog = this.logSource.asObservable();

  private stateSource = new BehaviorSubject<boolean>(true);
  stateClear = this.stateSource.asObservable();

  constructor() {
    // this.logs = [
    //   {
    //     id: "1",
    //     text: "Generated Components",
    //     date: new Date("12/26/2017 12:54:23")
    //   },
    //   {
    //     id: "2",
    //     text: "Added Bootstrap",
    //     date: new Date("12/27/2017 09:33:23")
    //   },
    //   {
    //     id: "3",
    //     text: "Added Logs Component",
    //     date: new Date("12/29/2017 12:00:23")
    //   }
    // ];

    this.logs = [];
  }

  getLogs(): Observable<Log[]> {
    //check to see if anything is in local storage
    if (localStorage.getItem("logs") === null) {
      this.logs = [];
    } else {
      this.logs = JSON.parse(localStorage.getItem("logs"));
    }
    return of(
      this.logs.sort((a, b) => {
        return (b.date = a.date);
      })
    );
  }

  setFormLog(log: Log) {
    this.logSource.next(log);
  }

  addLog(log: Log) {
    this.logs.unshift(log);

    // Add to local storage: only stores strings
    localStorage.setItem("logs", JSON.stringify(this.logs));
  }

  updateLog(log: Log) {
    this.logs.forEach((cur, index) => {
      if (log.id === cur.id) {
        this.logs.splice(index, 1);
      }
    });
    this.logs.unshift(log);

    // Add to local storage:
    localStorage.setItem("logs", JSON.stringify(this.logs));
  }

  deleteLog(log: Log) {
    this.logs.forEach((cur, index) => {
      if (log.id === cur.id) {
        this.logs.splice(index, 1);
      }
    });

    // Delete from local storage:
    localStorage.setItem("logs", JSON.stringify(this.logs));
  }

  clearState() {
    this.stateSource.next(true);
  }
}



